* {
    /* change the default since I haven't gotten to accounting borders yet*/
    box-sizing: border-box;
}

#container {
    border: 3px solid #6495ED;
    display: inline-flex;
    flex-direction: column;
    -webkit-border-radius: 12px;
    -moz-border-radius: 12px;
    border-radius: 12px;
    padding: 15px 15px;
}

.boxes {
    float: left;
    display: flex;
    flex-direction: column;
}

#container > div {
    display: flex;
    flex-direction: row;
}

.versions {
    flex-grow: 3;
}

.boxes > h2 {
    text-align: center;
}

textarea {
    flex-grow: 1;
    min-width: 100%;
}

#row2 {
    justify-content: space-evenly;
}

#panel {
    display: flex;
    font-weight: bold;
}

#panel > div {
    display: block;
}
#panel > div > div {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around;
}

#panel fieldset[name='boxControls'], #panel fieldset[name='colors'] {
    flex-direction: column;
    justify-content: space-evenly;
    align-content: center;
}

#panel fieldset[name='colors'] {
    align-content: stretch;
}

#panel fieldset, #colors {
    display: inline-flex;
    flex-wrap: wrap;
    flex-grow: 1;
    justify-content: center;
}

#colors {
    color: unset;
    justify-content: space-around;
}

.inputLabel {
    display: inline-flex;
    align-items: center;
}

#colors > .inputLabel {
    display: flex;
    justify-content: space-evenly;
}
#colors > .inputLabel > span {
    flex-grow: 1;
}
#colors > .inputLabel > input {
    align-self: flex-end;
}

#reset-color {
    margin-top: 1px
}


#export > fieldset {
    max-width: fit-content;
    align-self: center;
}
#export > textarea {
    min-width: 100%;
    resize: both;
}

#panel, button {
    align-self: center;
    text-align: center;
}

/* Left over from react creation, but still using */
.App {
    text-align: center;
}

.App-header {
    background-color: #282c34;
    min-height: 100vh;
    /*font-size: calc(10px + 2vmin);*/
    color: white;
}

.App-link {
    color: #61dafb;
}
